.nav{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    z-index: 1;
    height: 30px;
    justify-content: space-between;
    padding: 20px;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_black{
    background-color: #111;

}
.logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}
.avatar{
    position: fixed;
    width: 30px;
    right: 20px;
    object-fit: contain;
}
.myNav{
    color: red;
}